import request from '@/utils/request'

// 
export function listResults(query) {
    return request({
        url: '/bidding/results/list',
        method: 'get',
        params: query
    })
}

// 
export function getResults(gsId) {
    return request({
        url: '/bidding/results/' + gsId,
        method: 'get'
    })
}

// 
export function addResults(data) {
    return request({
        url: '/bidding/results',
        method: 'post',
        data: data
    })
}

// 
export function updateResults(data) {
    return request({
        url: '/bidding/results',
        method: 'put',
        data: data
    })
}

// 
export function delResults(gsId) {
    return request({
        url: '/bidding/results/' + gsId,
        method: 'delete'
    })
}

export function selectResultSupp(sid) {
    return request({
        url: '/bidding/results/selectResultSupp/'+sid,
        method: 'get',
    })
}

export function selectResultAndCandidate(sid) {
    return request({
        url: '/bidding/results/selectResultAndCandidate/'+sid,
        method: 'get',
    })
}
