import request from '@/utils/request'

// 
export function listPro(query) {
    return request({
        url: '/basic/pro/list',
        method: 'get',
        params: query
    })
}

export function check(query) {
    return request({
        url: '/basic/pro/checkIsJoin',
        method: 'get',
        params: query
    })
}

// 
export function getPro(gfId) {
    return request({
        url: '/basic/pro/' + gfId,
        method: 'get'
    })
}

// 
export function addPro(data) {
    return request({
        url: '/basic/pro',
        method: 'post',
        data: data
    })
}

// 
export function updatePro(data) {
    return request({
        url: '/basic/pro',
        method: 'put',
        data: data
    })
}

// 
export function delPro(gfId) {
    return request({
        url: '/basic/pro/' + gfId,
        method: 'delete'
    })
}
