<template>
  <div>
    <div style="height: 158px" class="tou">
      <h2 style="text-align: center;padding: 52px 0 16px;font-size: 26px">XX</h2>
      <p style="color: #f83e3d;text-align: center;font-weight: 400">
        </p>
    </div>
    <div style="width: 1400px;margin: 20px auto 0;background-color: #FFFFFF;">
      <el-form ref="form" :model="form" :rules="rules" label-width="560px" style="padding: 30px 0">
        <el-form-item label="" prop="hName" class="form-input">
          <el-input v-model="form.hName" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hCreditCode" class="form-input">
          <el-input v-model="form.hCreditCode" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hIncorporation" class="form-input">
          <el-input v-model="form.hIncorporation" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hStartTime" class="form-input">
          <el-date-picker clearable
                          v-model="form.hStartTime"
                          type="date"
                          value-format="yyyy-MM-dd"
                          placeholder=""
                          style="width: 340px">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="hJuridical" class="form-input">
          <el-input v-model="form.hJuridical" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hJuridicalIdentity" class="form-input">
          <el-input v-model="form.hJuridicalIdentity" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" class="form-input">
          <el-upload
              ref="upload1"
              :action="url"
              :on-change="changeFileLength"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
              :auto-upload="false"
              :limit="1"
              :on-success="handleFileSuccess"
              accept=".png, .jpg">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="hCopies.dialogVisible">
            <img width="100%" :src="hCopies.dialogImageUrl" alt=""/>
          </el-dialog>
          <p style="color: #999999;line-height: 18px;font-size: 12px">
            <br/>
            1<br/>
            2JPGPNGJPEG1M
          </p>
        </el-form-item>
        <el-form-item label="" class="form-input">
          <el-upload
              ref="upload2"
              :action="url"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview1"
              :on-remove="handleRemove1"
              :on-change="changeFileLength1"
              :on-success="handleFileSuccess1"
              :auto-upload="false"
              :limit="2"
              accept=".png, .jpg"
              multiple>
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="hJuridicalCopies.dialogVisible">
            <img width="100%" :src="hJuridicalCopies.dialogImageUrl" alt=""/>
          </el-dialog>
          <p style="color: #999999;line-height: 18px;font-size: 12px">
            <br/>
            1<br/>
            2JPGPNGJPEG1M<br/>
            3
          </p>
        </el-form-item>
        <el-form-item label="" prop="ywName" class="form-input">
          <el-input v-model="form.ywName" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="ywPhone" class="form-input">
          <el-input v-model="form.ywPhone" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="ywIdcrad" class="form-input">
          <el-input v-model="form.ywIdcrad" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" class="form-input">
          <el-upload
              ref="upload3"
              :action="url"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview2"
              :on-remove="handleRemove2"
              :on-change="changeFileLength2"
              :on-success="handleFileSuccess2"
              :auto-upload="false"
              :limit="2"
              accept=".png, .jpg"
              multiple>
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="ywScanIdcard.dialogVisible">
            <img width="100%" :src="ywScanIdcard.dialogImageUrl" alt=""/>
          </el-dialog>
          <p style="color: #999999;line-height: 18px;font-size: 12px">
            <br/>
            1<br/>
            2JPGPNGJPEG1M<br/>
            3
          </p>
        </el-form-item>
        <el-form-item label="" prop="ywMailbox" class="form-input">
          <el-input v-model="form.ywMailbox" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hLoginAccount" class="form-input">
          <el-input v-model="form.hLoginAccount" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="hPassword" class="form-input">
          <el-input v-model="form.hPassword" type="password" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
        <el-form-item label="" prop="checkPass" class="form-input">
          <el-input v-model="form.checkPass" type="password" placeholder="" style="width: 340px"
                    @keyup.enter.native="regist" clearable/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer" style="text-align: center;padding-bottom: 50px">
        <el-button type="primary" @click="regist" style="width: 380px"></el-button>
        <el-button @click="cancel" style="margin-left: 30px"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
import {addSupplier} from "@/api/regist";
import {onlyHCreditCode} from "@/api/system/supplier";

export default {
  data() {
    return {
      hCopies: {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      },
      hJuridicalCopies: {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      },
      ywScanIdcard: {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      },
      // 
      // url: process.env.VUE_APP_BASE_API + "/basic/supplier/upload1",
      url: "/prod-api" + "/basic/supplier/upload1",
      form: {
        zrId: null,
        hName: null,
        hCreditCode: null,
        hIncorporation: null,
        hStartTime: null,
        hJuridical: null,
        hJuridicalIdentity: null,
        hLoginAccount: null,
        hPassword: null,
        hCopies: null,
        hJuridicalCopies: null,
        checkPass: null,
        ywName: null,
        ywPhone: null,
        ywIdcrad: null,
        ywMailbox: null,
        ywScanIdcard: null
      },
      rules: {
        hName: [
          {required: true, message: "", trigger: "blur"}
        ],
        hCreditCode: [
          {required: true, message: "", trigger: "blur"},
          {
            pattern: /^[A-Z0-9]{18}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        hIncorporation: [
          {required: true, message: "", trigger: "blur"}
        ],
        hStartTime: [
          {required: true, message: "", trigger: "blur"}
        ],
        hJuridical: [
          {required: true, message: "", trigger: "blur"}
        ],
        hJuridicalIdentity: [
          {required: true, message: "", trigger: "blur"},
          {
            pattern: /(^\d{17}(\d|X|x)$)/,
            message: '',
            trigger: 'blur'
          }
        ],
        ywPhone: [
          {required: true, message: "", trigger: "blur"},
          {
            pattern: /^1[3456789]\d{9}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        ywName: [
          {required: true, message: "", trigger: "blur"}
        ],
        ywIdcrad: [
          {required: true, message: "", trigger: "blur"},
          {
            pattern: /(^\d{17}(\d|X|x)$)/,
            message: '',
            trigger: 'blur'
          }

        ],
        ywMailbox: [
          {required: true, message: "", trigger: "blur"},
          {
            type: 'email',
            message: '',
            trigger: 'blur'
          }
        ],
        hLoginAccount: [
          {required: true, message: "", trigger: "blur"},
          {
            pattern: /^[a-zA-Z0-9_]{4,16}$/,
            message: '4-16',
            trigger: 'blur'
          }
        ],
        hPassword: [
          {required: true, message: "", trigger: "blur"},
          {
            min: 6,
            max: 16,
            message: '616',
            trigger: 'blur'
          }
        ],
        checkPass: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: (rule, value, callback) => {
              if (value !== this.form.hPassword) {
                callback(new Error('!'));
              } else {
                callback();
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  }, methods: {
    reset() {
      this.form = {
        zrId: null,
        hName: null,
        hCreditCode: null,
        hIncorporation: null,
        hStartTime: null,
        hJuridical: null,
        hJuridicalIdentity: null,
        hLoginAccount: null,
        hPassword: null,
        hCopies: null,
        hJuridicalCopies: null,
        checkPass: null,
        ywName: null,
        ywPhone: null,
        ywIdcrad: null,
        ywMailbox: null,
        ywScanIdcard: null
      }
      this.hCopies = {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      }
      this.hJuridicalCopies = {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      }
      this.ywScanIdcard = {
        dialogImageUrl: '',
        dialogVisible: false,
        // 
        uploadFiles: [],
        // 
        filesLength: 0,
      }
    },
    // 
    cancel() {
      this.reset();
      this.$router.back()
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
      this.hCopies.filesLength = fileList.length
    },
    handlePictureCardPreview(file) {
      this.hCopies.dialogImageUrl = file.url;
      this.hCopies.dialogVisible = true;
    },
    // 
    changeFileLength(file, fileList) {
      this.hCopies.filesLength = fileList.length
    },
    // 
    handleFileSuccess(response, file, fileList) {
      this.hCopies.uploadFiles.push(file);
      //
      if (this.hCopies.uploadFiles.length === this.hCopies.filesLength) {
        let hhh = fileList.map(obj => {
          let newObj = obj;
          delete newObj.url;
          newObj.url = obj.response.data.url;
          delete newObj.name;

          newObj.name = obj.response.data.name;
          delete newObj.response;
          delete newObj.raw;
          delete newObj.percentage;
          delete newObj.status;
          delete newObj.uid;
          return newObj;
        })
        this.form.hCopies = JSON.stringify(hhh)
        this.$refs.upload2.submit();
      }
    },

    //
    handleRemove1(file, fileList) {
      console.log(file, fileList);
      this.hJuridicalCopies.filesLength = fileList.length
    },
    handlePictureCardPreview1(file) {
      this.hJuridicalCopies.dialogImageUrl = file.url;
      this.hJuridicalCopies.dialogVisible = true;
    },
    changeFileLength1(file, fileList) {
      this.hJuridicalCopies.filesLength = fileList.length
    },
    // 
    handleFileSuccess1(response, file, fileList) {
      this.hJuridicalCopies.uploadFiles.push(file);
      //
      if (this.hJuridicalCopies.uploadFiles.length === this.hJuridicalCopies.filesLength) {
        let hhh = fileList.map(obj => {
          let newObj = obj;
          delete newObj.url;
          newObj.url = obj.response.data.url;
          delete newObj.name;

          newObj.name = obj.response.data.name;
          delete newObj.response;
          delete newObj.raw;
          delete newObj.percentage;
          delete newObj.status;
          delete newObj.uid;
          return newObj;
        })
        this.form.hJuridicalCopies = JSON.stringify(hhh)
        this.$refs.upload3.submit();
      }
    },

    //
    handleRemove2(file, fileList) {
      console.log(file, fileList);
      this.ywScanIdcard.filesLength = fileList.length
    },
    handlePictureCardPreview2(file) {
      this.ywScanIdcard.dialogImageUrl = file.url;
      this.ywScanIdcard.dialogVisible = true;
    },
    changeFileLength2(file, fileList) {
      this.ywScanIdcard.filesLength = fileList.length
    },
    // 
    handleFileSuccess2(response, file, fileList) {
      this.ywScanIdcard.uploadFiles.push(file);
      //
      if (this.ywScanIdcard.uploadFiles.length === this.ywScanIdcard.filesLength) {
        let hhh = fileList.map(obj => {
          let newObj = obj;
          delete newObj.url;
          newObj.url = obj.response.data.url;
          delete newObj.name;
          newObj.name = obj.response.data.name;
          delete newObj.response;
          delete newObj.raw;
          delete newObj.percentage;
          delete newObj.status;
          delete newObj.uid;
          return newObj;
        })
        this.form.ywScanIdcard = JSON.stringify(hhh)
        addSupplier(this.form).then(res => {
          if (res.data.code === 200) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.reset()
          }
        })
      }
    },
    regist() {
      this.$refs.form.validate(valid => {
        if (valid) {
          onlyHCreditCode({"hCreditCode": this.form.hCreditCode}).then(res => {
            if (res.data.data != null) {
              this.$message.error('')
            } else {
              if (this.hCopies.filesLength === 1 && this.hJuridicalCopies.filesLength === 2 && this.ywScanIdcard.filesLength === 2) {
                this.$refs.upload1.submit();
              } else {
                this.$message.error('');
              }
            }
          })
        }
      })
    }
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.tou {
  background: url('/public/images/bj.2c9500fa.png') 50%;
  background-size: 1920px 188px;
}

body {
  background-color: #F9F9F9;
}
</style>
